
import {Link} from "react-router-dom"
import './nav.css'

function Nav() {
  return (
      <div className='navbar'>
          <nav>
              <Link to="/" className="brand">
              <h1>Cooking Ninja</h1>
              </Link>
              <Link to="/create">Create Recipes</Link>
      </nav>
      </div>
  )
}

export default Nav;